body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{
  text-align: center;
  height: 100vh;
}

.logo {
  position: fixed !important;
  top: -5% !important;
  left: -5% !important;
  height: 110vh;
  width: 110vw;
}

.moving{
  font-family: 'Ma Shan Zheng', cursive;
  margin: 0;
  padding: 50px ; 
  color: aliceblue;
  font-size: 30px;
  z-index: 100;
}
.header{
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}
.main{
  z-index: 1000;
}
.intro{
  height: 100vh;
  background-color: black;
  opacity: 0.9;
  display: flex;
  justify-content: center;
  align-items: center;
}
.intro h3, .intro p{
  font-family: 'Ma Shan Zheng', cursive;
  color: aliceblue;
}